.timetable__location-header {
  position: relative;
  height: 45px;
  white-space: nowrap; }
  .timetable__location-header:hover .timetable__location-description {
    -webkit-transition: none;
    -o-transition: none;
    transition: none;
    pointer-events: unset;
    opacity: 1; }

.timetable__location-title {
  text-align: center;
  vertical-align: middle; }

.timetable__location-description {
  pointer-events: none;
  width: 100%;
  white-space: normal;
  position: absolute;
  opacity: 0;
  -webkit-transition: 0.2s ease-in opacity;
  -o-transition: 0.2s ease-in opacity;
  transition: 0.2s ease-in opacity;
  background-color: #FFFFFF; }



