.timetable__time-slots {
  margin-top: 45px;
  background-color: white; }

.timetable__content {
  display: -ms-flexbox;
  display: flex;
  margin-left: 10px; }

@supports ((position: -webkit-sticky) or (position: sticky)) {
  .timetable .timetable__time-slots {
    position: -webkit-sticky;
    position: sticky;
    left: 0; } }



